import React from 'react'
import { Container } from '../style';
import styled from 'styled-components';
import Img from 'gatsby-image';
import {documentToReactComponents} from '@contentful/rich-text-react-renderer';
import Zoom from 'react-medium-image-zoom'
import ArrowIcon from '../../assets/svgs/graph-design/graph-design-training-path-arrow.svg';
import CheckIcon from '../../assets/svgs/graph-design/graph-design-training-path-check.svg';


const Arrow = styled(props => <ArrowIcon {...props}/>)`
    width: 3vw;
    margin-left: 1.5vw;
    fill: var(--granich-red);
    @media only screen and (max-width: 575px) {
        transform: rotate(90deg);
        width: 7vw;
        margin-left: 0;
        margin-top: 1vw;
    }
`

const Check = styled(props => <CheckIcon {...props}/>)`
    width: 3vw;
    margin-left: 1.5vw;
    fill: var(--granich-red);
    @media only screen and (max-width: 575px) {
        width: 7vw;
        margin-left: 0.5vw;
        margin-top: 1.5vw;
    }
`


const TrainingPathSection = styled.section`
    margin-bottom: 4vw;
    margin-top: 4vw;
    @media only screen and (max-width: 575px) {
        margin-bottom: 5vw;
        margin-top: 5vw;
    }
    
`


const TrainingPathWrapper = styled.div`

    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 1.5vw;
    grid-row-gap: 1.5vw;
    @media only screen and (max-width: 575px) {
        margin-bottom: 5vw;
        grid-template-columns: 1fr;
        grid-row-gap: 4vw;
    }
`

const TrainingPathBlockWrapper = styled.div`
    background: white;
    display: flex;
    flex-direction: column;

    border-radius: 0.6vw;
    position: relative;
    :before {
        content: '';
        width: 23vw;
        height: 23vw;
        // background: var(--granich-red);
        background: #f2f2f2;
        position: absolute;
        top: 1.25vw;;
        left: 50%;
        transform: translateX(-50%);
        border-radius: 100vw;
    }
    @media only screen and (max-width: 575px) {
        border-radius: 2.5vw;
        :before {
            content: '';
            width: 50vw;
            height: 50vw;
            background: var(--granich-red);
            position: absolute;
            top: 4vw;;
            left: 50%;
            transform: translateX(-50%);
            border-radius: 100vw;
        }
    }

`
const TrainingPathBlockContainer = styled.div`
    padding: 2.7vw 2vw 2vw;
    margin-bottom: 1vw;


    @media only screen and (max-width: 575px) {
        padding: 5vw 5vw 5vw;
        display: flex;
        margin-bottom: 2vw;
    }
`



const TrainingPathBlockNumber = styled.div`
    font-family: EB Garamond;
    font-style: italic;
    font-size: 5.4vw;
    font-weight: 500;
    position: relative;
    letter-spacing: 0;
    line-height: 1;
    margin-left: -0.1vw;
    margin-bottom: 1vw;
    @media only screen and (max-width: 575px) {
        font-size: 13vw;
        margin-bottom: 4vw;
        margin-left: 0;
        display: flex;
        flex-direction: column;
        margin-right: 6vw;
        margin-top: -0.5vw;
    }
`

const TrainingPathBlockText = styled.div`

    line-height: 1.35;
    p {
        color: var(--granich-grey);
        font-size: 1.15vw;
        letter-spacing: -0.01vw;
        b {
            display: block;
            font-weight: 600;
            color: var(--granich-black);
            margin-bottom: 0.35vw;
        }
    }
    @media only screen and (max-width: 575px) {
        p {
            font-size: 3.7vw;
            b {
                margin-bottom: 1vw;
            }
        }
        margin-top: 2vw;
    }


`

const TrainingPathBlockImage = styled.div`
    width: 100%;
    height: auto;
    transition: transform 0.3s ease;
    margin-top: auto;
    margin: 0 auto;
    margin-top: auto;
    margin-bottom: 2vw;


    div {
        width: 100%;
        height: 20vw;
    }


    @media only screen and (max-width: 575px) {
        width: 82%;
        margin-bottom: 7vw;
        :hover {
            transform: none;
    
        }
    }
`



const TrainingPathImage = styled(props => <Img {...props}/>)`
    width: 100%;
    height: 8.9vw;
    background-size: cover;
    box-shadow: 0 0 0.4vw rgba(0,0,0,0.4);
    @media only screen and (max-width: 575px) {
        height: 44vw;    
        box-shadow: 0 0 1.8vw rgba(0,0,0,0.4);
    }

`

const TrainingPathSectionTitle = styled.h2`
    color: var(--granich-black);
    font-size: 4.55vw;
    letter-spacing: -0.2vw;
    font-weight: 700;
    line-height: 1;
    margin-bottom: 2.5vw;
    span {
        text-transform: uppercase;
        font-weight: 700;
    }
    @media only screen and (max-width: 575px) {
        font-size: 11vw;
        letter-spacing: -0.7vw;
        line-height: 1;
        margin-bottom: 4vw;
    }
`



const CourseTrainingPath = ({data}) => {
    return (
        <TrainingPathSection>
            <Container>
                <TrainingPathSectionTitle>   <span></span> </TrainingPathSectionTitle>
                <TrainingPathWrapper>
                    {data.edges.map((block, idx) => {
                        return ( 
                            <TrainingPathBlockWrapper key={block.node.id}>
                                <TrainingPathBlockContainer>
                                <TrainingPathBlockImage>
                                        <Zoom>
                                            <TrainingPathImage fluid={block.node.trainingPathImage.fluid}></TrainingPathImage>
                                        </Zoom>
                                    </TrainingPathBlockImage>
                                    {/* <TrainingPathBlockNumber >{block.node.trainingPathTitle}{idx === 3 ? <Check/> : <Arrow/>}</TrainingPathBlockNumber> */}
                                    <TrainingPathBlockText>{documentToReactComponents(block.node.trainingPathText.json)}</TrainingPathBlockText>
                                </TrainingPathBlockContainer>
                                {/* <TrainingPathImageContainer> */}
                                    
{/* 
                                </TrainingPathImageContainer> */}
                            </TrainingPathBlockWrapper>
                        )
                    })}

                </TrainingPathWrapper>
            </Container>
        </TrainingPathSection>
    )
}

export default CourseTrainingPath
